<template>
  <v-container class="justify-center">
    <v-layout justify-center align-center>
      <v-col cols="8">
        <v-row align="center">
          <v-img src="logo_bb_offset_9.0.svg" max-width="64"></v-img>

          <h2>Engora Catalog Search ð›¼</h2>

          <v-tooltip bottom max-width="500">
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                class="ml-2"
                size="26"
                color="primary"
                v-bind="attrs"
                v-on="on"
              >
                mdi-information
              </v-icon>
            </template>
            <HowToText />
          </v-tooltip>
        </v-row>

        <v-row>
          <div class="column mt-10">
            <!--            <h5>{{ s3 }}</h5>-->
            <!--            <v-slider-->
            <!--              label="a"-->
            <!--              step="0.3"-->
            <!--              min="0"-->
            <!--              max="100"-->
            <!--              ticks-->
            <!--              v-model="s3"-->
            <!--              thumb-label-->
            <!--              style="width: 200px"-->
            <!--            ></v-slider>-->
            <!--            <h5>{{ s4 }}</h5>-->
            <!--            <v-slider-->
            <!--              label="Step = 0.1, min = -1, max = 1"-->
            <!--              step="0.1"-->
            <!--              min="-1"-->
            <!--              max="1"-->
            <!--              v-model="s4"-->
            <!--              thumb-label-->
            <!--            ></v-slider>-->
          </div>
        </v-row>
        <v-row align="center">
          <v-text-field
            class="shrink"
            label="Product"
            v-model="searchTerm"
          ></v-text-field>
        </v-row>

        <v-row>
          <v-btn
            @click="addRequirement"
            :disabled="isAddRequirementDisabled"
            elevation="1"
          >
            Add Requirement
          </v-btn>
        </v-row>
        <v-row>
          <v-list-item v-for="(req, index) in requirements" :key="req.id">
            <Requirement
              :index="index"
              :req="req"
              @remove="removeRequirement($event)"
              @typeChange="setRequirementType($event)"
              @unitChange="setRequirementUnit($event)"
              @specNameInput="setRequirementSpecName($event)"
            />
          </v-list-item>
        </v-row>

        <v-row>
          <v-btn
            depressed
            color="primary"
            elevation="2"
            class="mt-4 mb-4"
            :disabled="!isSearchable"
            :loading="isGettingSearchResults"
            @click="searchProducts"
          >
            Search
          </v-btn>
        </v-row>

        <v-row>
          <h2>Try one of these searches:</h2>
          <div class="mt-4 mb-4">
            <v-btn
              elevation="1"
              class="mr-4 mb-4"
              @click="setRequirementsPreset(presetsKeys.MOTORS)"
              >Motors by torque and speed</v-btn
            >
            <v-btn
              elevation="1"
              class="mr-4 mb-4"
              @click="setRequirementsPreset(presetsKeys.BRUSHLESS_MOTORS)"
              >Brushless motors by weight and thrust</v-btn
            >
            <v-btn
              elevation="1"
              class="mb-4"
              @click="setRequirementsPreset(presetsKeys.BATTERIES)"
              >Batteries by capacity</v-btn
            >
          </div>
        </v-row>

        <v-row v-if="resultsArrived">
          <Results :results="results" :axes="axes" />
        </v-row>
      </v-col>
    </v-layout>
  </v-container>
</template>

<script>
import Requirement from "@/components/Requirement";
import HowToText from "@/components/HowToText";
import Results from "@/components/Results";
import axios from "axios";
export default {
  name: "Search",
  components: {
    Requirement,
    HowToText,
    Results,
  },
  computed: {
    isAddRequirementDisabled() {
      return !(this.requirements.length < this.maxRequirements);
    },
    resultsArrived() {
      return this.results.length > 0;
    },
    isSearchable() {
      return this.searchTerm.length > 0;
    },
  },
  data: () => ({
    searchTerm: "",
    requirements: [],
    queryString: [],
    maxRequirements: 5,
    presetSearchTerms: {
      MOTORS: "motor",
      BRUSHLESS_MOTORS: "motor",
      BATTERIES: "battery",
    },
    presetsKeys: {
      MOTORS: "MOTORS",
      BRUSHLESS_MOTORS: "BRUSHLESS_MOTORS",
      BATTERIES: "BATTERIES",
    },
    presets: {
      MOTORS: [
        {
          id: 1,
          type: "numeric",
          value: "speed",
          unit: "angular_speed",
        },
        {
          id: 2,
          type: "numeric",
          value: "torque",
          unit: "torque",
        },
      ],
      BRUSHLESS_MOTORS: [
        {
          id: 3,
          type: "numeric",
          value: "weight",
          unit: "mass",
        },
        {
          id: 4,
          type: "numeric",
          value: "thrust",
          unit: "force",
        },
        {
          id: 5,
          type: "category",
          value: "brushless",
        },
      ],
      BATTERIES: [
        {
          id: 6,
          type: "numeric",
          value: "capacity",
          unit: "current_capacity",
        },
        {
          id: 7,
          type: "numeric",
          value: "voltage",
          unit: "voltage",
        },
      ],
    },
    results: [],
    activePreset: null,
    axes: [],
    isGettingSearchResults: false,
  }),
  methods: {
    addRequirement() {
      if (this.requirements.length < this.maxRequirements) {
        const uniqueID = this.getUniqueID();
        this.requirements.push({
          id: uniqueID,
          type: "numeric",
          value: "",
          unit: "any",
        });
      }
    },
    removeRequirement(indexToRemove) {
      if (this.requirements[indexToRemove]) {
        this.requirements.splice(indexToRemove, 1);
      }
    },
    setRequirementsPreset(key) {
      if (this.presets[key]) {
        this.searchTerm = this.presetSearchTerms[key];
        this.requirements.splice(0, this.requirements.length);
        this.activePreset = this.presets[key];
        this.requirements.push(...this.presets[key]);
      }
    },
    setRequirementType(data) {
      this.requirements[data.index].type = data.type;
    },
    setRequirementUnit(data) {
      this.requirements[data.index].unit = data.unit;
    },
    setRequirementSpecName(data) {
      this.requirements[data.index].value = data.value;
    },
    setQueryString() {
      this.queryString = [];
      this.requirements.forEach((requirement) => {
        // Temp - until backend naming is changed use old format "unit" && "property"
        let requirementType = requirement.type;
        if (requirementType === "numeric") {
          requirementType = "unit";
        }
        if (requirementType === "category") {
          requirementType = "property";
        }

        this.queryString.push([
          requirementType,
          requirement.value,
          requirement.unit,
        ]);
      });
    },
    initAxes() {
      this.axes = [];
      for (const [key] of Object.entries(this.results[0].values)) {
        this.axes.push(key);
      }
    },
    handleNoResultsSearch() {
      // TBD - pop a modal stating there are no results and guiding the user on what they can search ?
    },
    resetSearch() {
      this.results.splice(0, this.results.length);
    },
    searchProducts() {
      this.isGettingSearchResults = true;
      this.resetSearch();
      this.setQueryString();
      // TODO - before going live change to /search
      axios
        .put("https://search.engora.tech/search", {
          product: this.searchTerm,
          qs: this.queryString,
        })
        .then((response) => {
          this.isGettingSearchResults = false;
          if (response.data.length === 0) {
            this.handleNoResultsSearch();
          }
          // console.log("data ", response.data);
          // TODO - TBD if data length 0 show modal that helps search better
          if (response.data.length > 0) {
            this.results = response.data;
            this.initAxes();
          }
        });
    },
    // * * * Util * * *
    getUniqueID() {
      return Math.floor(Math.random() * 100000);
    },
  },
};
</script>
