<template>
  <div>
    <div class="d-flex pt-4 pb-4">
      <v-spacer />
      <v-btn @click="toggleAddColumn">
        <v-icon v-if="!isAddingColumn">mdi-plus</v-icon>
        <v-icon v-if="isAddingColumn">mdi-close</v-icon>
      </v-btn>
    </div>

    <!--    newColumnKeys {{ newColumnKeys }}-->

    <div class="d-flex">
      <v-data-table
        :headers="headers"
        :items="tableItems"
        :items-per-page="5"
        class="elevation-1"
        :hide-default-footer="true"
        disable-pagination
      >
        <template #item.name="{ item }">
          <a
            :href="absHref(item.link)"
            target="_blank"
            style="text-decoration: none"
          >
            {{ item.name }}
          </a>
        </template>

        <template #item.price="{ item }">
          {{ formatPrice(item.price) }}
        </template>

        <!--      <template #item.default="{ item }">{{ item + "-&#45;&#45;" }}</template>-->
      </v-data-table>

      <AddColumn
        v-if="isAddingColumn && parser"
        :exampleRow="exampleRow"
        :parser="parser"
        @addColumn="addColumn($event)"
      ></AddColumn>
    </div>
  </div>
</template>

<script>
import AddColumn from "@/components/AddColumn";
export default {
  props: ["headers", "tableItems", "parser", "exampleRow", "newColumnKeys"],
  components: {
    AddColumn,
  },
  data: () => ({
    isAddingColumn: true,
  }),
  methods: {
    absHref(url) {
      return `https://${url}`;
    },
    formatPrice(value) {
      if (value) {
        return `$${value.toFixed(2)}`;
      } else {
        return value;
      }
    },
    toggleAddColumn() {
      this.isAddingColumn = !this.isAddingColumn;
    },
    addColumn(payload) {
      this.$emit("addColumn", payload);
      this.isAddingColumn = false; // zeroize destroy addcolumn
    },
  },
};
</script>
