<template>
  <div class="mt-2">
    <v-row class="align-center">
      <h2 class="mr-4">Results</h2>
      <v-btn @click="expandAll" x-small> Expand All</v-btn>
      <v-btn @click="collapseAll" x-small class="ml-4"> Close All</v-btn>

      <v-spacer />

      <v-btn
        class="ma-2 mr-4"
        small
        outlined
        download
        :href="csv"
        @click="downloadCSVData"
        >Download CSV
      </v-btn>
      <AxesManipulation
        :axes="axes"
        @yAxisChanged="yAxisChanged($event)"
        @xAxisChanged="xAxisChanged($event)"
      />
    </v-row>

    <v-row>
      <v-col cols="4">
        <Filters
          :panels="panels"
          :filters="filters"
          @panelChanged="updatePanels($event)"
          @activeRangeChanged="activeRangeChanged"
        />
      </v-col>
      <v-col cols="8">
        <!--  Any Changes to active range should be set in Dynamic instance only-->
        <v-card elevation="1" class="mb-4 pt-4 pb-2">
          <Scatterplot
            chartId="engora-products-scatterplot-dynamic"
            :data="dynamicScatterplotData"
            :xAxisLabel="xAxisLabel"
            :yAxisLabel="yAxisLabel"
          />

          <Scatterplot
            chartId="engora-products-scatterplot-static"
            :isStatic="true"
            :data="staticScatterplotData"
            :xAxisLabel="xAxisLabel"
            :yAxisLabel="yAxisLabel"
          />
        </v-card>

        <!--        TODO - for filtering table contents by currentFiltersWithActiveRanges-->
        <!--        Debug {{ currentFiltersWithActiveRanges }}-->

        <ResultsTable
          :headers="headers"
          :tableItems="tableItems"
          :exampleRow="exampleRow"
          :parser="parser"
          :newColumnKeys="newColumnKeys"
          @addColumn="addColumn"
        />
      </v-col>
    </v-row>
  </div>
</template>

<script>
import Scatterplot from "@/components/Scatterplot";
import Filters from "@/components/Filters";
import AxesManipulation from "@/components/AxesManipulation";
import ResultsTable from "@/components/ResultsTable";
import FormulaParser from "hot-formula-parser/parser";

export default {
  props: ["results", "axes"],
  components: {
    Scatterplot,
    Filters,
    AxesManipulation,
    ResultsTable,
  },
  data: () => ({
    headers: [],
    tableItems: [],
    staticScatterplotData: [],
    dynamicScatterplotData: [],
    outsideDynamicScatterplotData: [],
    xAxisLabel: "",
    yAxisLabel: "",
    csv: null,
    panels: [],
    capacityKey: "capacity (As)",
    filters: [],
    currentFiltersWithActiveRanges: {},
    parser: null,
    exampleRow: {},
    alphabet: [
      "a",
      "b",
      "c",
      "d",
      "e",
      "f",
      "g",
      "h",
      "i",
      "j",
      "k",
      "l",
      "m",
      "n",
      "o",
      "p",
      "q",
      "r",
      "s",
      "t",
      "u",
      "v",
      "w",
      "x",
      "y",
      "z",
    ], // currently supports 26 columns (A-Z) - can be extended by simply adding keys to this alphabet array
    newColumnKeys: [],
  }),
  methods: {
    initFilterPanels() {
      // currently all expanded
      this.panels = [];
      this.axes.forEach((axis, i) => {
        this.panels.push(i);
      });
    },
    updatePanels(i) {
      if (this.panels.includes(i)) {
        this.panels.splice(this.panels.indexOf(i), 1);
      } else {
        this.panels.push(i);
      }
    },
    initActiveAxes() {
      if (this.xAxisLabel.length === 0 && this.yAxisLabel.length === 0) {
        this.xAxisLabel = this.axes[0];

        // handle only price axes returned
        if (this.axes[1]) {
          this.yAxisLabel = this.axes[1];
        } else {
          this.yAxisLabel = this.axes[0];
        }
      }
    },
    setStaticScatterplotData() {
      this.staticScatterplotData = [];
      this.results.forEach((entry, index) => {
        // first index is X, second is Y
        let point = {
          id: index + 1,
          x: entry.values[this.xAxisLabel][0],
          y: entry.values[this.yAxisLabel][0],
          name: entry.product[2],
          isActive: true,
        };
        this.staticScatterplotData.push(point);
      });
    },
    initDynamicScatterplotData() {
      this.dynamicScatterplotData.push(...this.staticScatterplotData);
    },
    setDynamicScatterplotData() {
      let notActivePointColor = "#DBE2EA";
      let activePointColor = "#7cb5ec";

      this.staticScatterplotData = [];
      this.dynamicScatterplotData = [];
      this.outsideDynamicScatterplotData = [];
      // check if is in active range of all filters.
      this.results.forEach((e, index) => {
        let isInActiveRangesMap = [];
        // iterate over all current filters ranges
        for (const [key, value] of Object.entries(e.values)) {
          let min = this.currentFiltersWithActiveRanges[key][0];
          let max = this.currentFiltersWithActiveRanges[key][1];
          if (min <= value[0] && value[0] <= max) {
            isInActiveRangesMap.push(1);
          } else {
            isInActiveRangesMap.push(0);
          }
        }

        let point = {
          id: index + 1,
          x: e.values[this.xAxisLabel][0],
          y: e.values[this.yAxisLabel][0],
          name: e.product[2],
          isActive: true,
          color: activePointColor,
        };

        // insert only if point in N active ranges // outside if at least out of 1 range
        if (!isInActiveRangesMap.includes(0)) {
          this.dynamicScatterplotData.push(point);
          this.staticScatterplotData.push(point);
        } else {
          this.outsideDynamicScatterplotData.push(point);

          point.isActive = false;
          point.color = notActivePointColor;
          this.staticScatterplotData.push(point);
        }
      });
    },
    downloadCSVData() {
      let filename = "results.csv";
      let csvContent = "data:text/csv;charset=utf-8,";

      csvContent += '"' + "URL" + '","' + "Name" + '",';
      for (let axeLabel of this.axes) {
        csvContent += ' "' + axeLabel + '" ,';
      }
      csvContent += "\r\n";

      for (let d of this.results) {
        csvContent +=
          '"' +
          "https://" +
          d.product[1] +
          '","' +
          d.product[2].replace('"', '""') +
          '",';
        for (let axeLabel of this.axes) {
          csvContent += "" + d.values[axeLabel][0] + ",";
        }
        csvContent += "\r\n";
      }
      this.csv = encodeURI(csvContent);

      let link = document.createElement("a");
      link.setAttribute("href", this.csv);
      link.setAttribute("download", filename);
      link.click();
    },
    yAxisChanged(newVal) {
      this.yAxisLabel = newVal;
      this.setStaticScatterplotData();
      this.setDynamicScatterplotData();
    },
    xAxisChanged(newVal) {
      this.xAxisLabel = newVal;
      this.setStaticScatterplotData();
      this.setDynamicScatterplotData();
    },
    expandAll() {
      this.panels = [];
      this.axes.forEach((axis, i) => {
        this.panels.push(i);
      });
    },
    collapseAll() {
      this.panels = [];
    },
    initTableHeaders() {
      this.headers = [{ text: "Name", value: "name" }];
      Object.keys(this.results[0].values).forEach((label) => {
        this.headers.push({
          text: label,
          value: label,
        });
      });
    },
    initTableItems() {
      this.tableItems = [];
      // console.log("results [0] values", this.results[0].values);
      this.results.forEach((entry) => {
        let tableItem = {
          name: entry.product[2],
          link: entry.product[1],
        };

        for (const [key, value] of Object.entries(entry.values)) {
          if (key !== "price") {
            // general formatting for values that are not Price / Name / Link
            tableItem[key] = value[0].toPrecision(3);
          } else {
            tableItem[key] = value[0];
          }
        }

        this.tableItems.push(tableItem);
      });

      // console.log("table items [0] ", this.tableItems[0]);
    },
    initExampleRow() {
      let indexCounter = 0;
      this.exampleRow = {};
      for (const [key, value] of Object.entries(this.results[0].values)) {
        this.exampleRow[this.alphabet[indexCounter].toUpperCase()] = {
          key: key,
          value: value[0],
        };
        indexCounter++;
      }
    },
    initFiltersByCurrentAxes() {
      this.axes.forEach((axis) => {
        this.filters.push({
          key: axis,
          values: [],
        });
      });
    },
    initFiltersData() {
      this.results.forEach((entry) => {
        for (const [filter, value] of Object.entries(entry.values)) {
          this.filters.forEach((range) => {
            if (range.key === filter) {
              range.values.push(value[0]);
            }
          });
        }
      });

      this.filters.forEach((filter) => {
        filter.min = Math.min(...filter.values);
        filter.max = Math.max(...filter.values);
        filter.range = [filter.min, filter.max];

        this.currentFiltersWithActiveRanges[filter.key] = filter.range;
      });
    },
    updateFilters(updatedFilter) {
      this.currentFiltersWithActiveRanges[updatedFilter.key] =
        updatedFilter.activeRange;
    },
    activeRangeChanged(filter) {
      this.updateFilters(filter);
      this.setDynamicScatterplotData(filter);
    },
    initParser() {
      this.parser = new FormulaParser();
    },
    parse(string) {
      return this.parser.parse(string);
    },
    updateResultsData(payload) {
      this.results.forEach((entry) => {
        let indexCounter = 0;
        let tempValue = payload.formula.value;
        // parse formula with concrete row data
        for (const [, value] of Object.entries(entry.values)) {
          let columnID = this.alphabet[indexCounter].toUpperCase();
          let match = tempValue.match(columnID);
          if (match) {
            tempValue = tempValue.replace(columnID, value[0]);
          }
          indexCounter++;
        }
        // if new unique value add
        if (!(payload.formula.key in entry.values)) {
          const output = this.parse(tempValue);
          entry.values[payload.formula.key] = [output.result, output.result];
          this.shouldUpdateNewColumnKeys(payload.formula.key);
        }
      });
    },
    shouldUpdateNewColumnKeys(key) {
      let isNewKey = false;
      this.newColumnKeys.forEach((k) => {
        if (k === key) {
          isNewKey = true;
        }
      });

      if (!isNewKey) {
        this.newColumnKeys.push(key);
      }
    },
    removeColumnFromResults(key) {
      // TODO - if needed complete this
      this.results.forEach((entry) => {
        // if new unique value add
        if (key in entry.values) {
          // console.log("key exists in results ", key);
          // const output = this.parse(tempValue);
          // entry.values[payload.formula.key] = [output.result, output.result];
        }
      });
    },
    updateTableHeaders(payload) {
      let isNewHeader = true;
      this.headers.forEach((header) => {
        if (header.value === payload.header.value) {
          isNewHeader = false;
        }
      });

      if (isNewHeader) {
        this.headers.push({
          text: payload.header.text,
          value: payload.header.value,
        });
      }
    },
    addColumn(payload) {
      // Currently mutates the results array
      this.updateResultsData(payload);
      this.initExampleRow();
      this.updateTableHeaders(payload);
      this.initTableItems();
      // console.log("after new column ", this.results);
    },
  },
  beforeMount() {
    this.expandAll();

    this.initTableHeaders();
    this.initTableItems();
    this.initExampleRow();

    this.initFilterPanels();
    this.initFiltersByCurrentAxes();
    this.initFiltersData();
    this.initParser();

    this.initActiveAxes();
    this.setStaticScatterplotData();
    this.initDynamicScatterplotData();
  },
};
</script>
